import { useState, useMemo, useEffect } from "react";
import { useRouter } from "next/router";
import Layout from "../components/Layout";
import { useAuth } from "../context/AuthContext";
import { useInventory } from "../context/InventoryContext";
import WarningIndicator from "../components/WarningIndicator";
import Modal from "../components/Modal";
import api from "../utils/api";
import toast from "react-hot-toast";

export default function ProductsPage() {
  const router = useRouter();
  const { user, hasPermission } = useAuth();
  const { getWarnings, printInventoryReport } = useInventory();

  // ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™ ÿßŸÑÿ¢ŸÜ ÿ™ÿ£ÿ™Ÿä ŸÖŸÜ ÿßŸÑÿ®ÿßŸÉ-ÿ•ŸÜÿØ
  const [products, setProducts] = useState([]);

  // ÿ®ÿ≠ÿ´ ŸàŸÅŸÑÿ™ÿ±ÿ© Ÿàÿ™ÿ±ÿ™Ÿäÿ®
  const [search, setSearch] = useState("");
  const [categoryFilter, setCategoryFilter] = useState("all");
  const [companyFilter, setCompanyFilter] = useState("all");
  const [sortByName, setSortByName] = useState("asc");
  const [filterLowStock, setFilterLowStock] = useState(false);
  const [filterNearExpiry, setFilterNearExpiry] = useState(false);
  const [filterExpired, setFilterExpired] = useState(false);

  // ÿπÿ±ÿ∂ ÿ™ŸÅÿßÿµŸäŸÑ
  const [showDetails, setShowDetails] = useState(false);
  const [selectedProduct, setSelectedProduct] = useState(null);

  // ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨
  const [showAddModal, setShowAddModal] = useState(false);
  const [newProduct, setNewProduct] = useState({
    name: "",
    sku: "",
    category: "",
    company: "",
    purchasePrice: "",
    price: "",
    quantity: "",
    minQty: 5,
    expiryDate: "",
  });

  // ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™ ŸÖŸÜ ÿßŸÑÿ®ÿßŸÉ-ÿ•ŸÜÿØ
  // ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™ ŸÖŸÜ ÿßŸÑÿ®ÿßŸÉ-ÿ•ŸÜÿØ (ŸÖÿπ ŸÖŸàÿßÿ°ŸÖÿ© ÿßŸÑÿ≠ŸÇŸàŸÑ)
const loadProducts = async () => {
  try {
    const res = await api.get("/products");

    const normalized = res.data.map((p) => ({
      id: p.id,
      name: p.name,
      sku: p.sku,
      category: p.category || p.category_name || "",
      company: p.company || p.company_name || "",
      purchasePrice: Number(p.purchasePrice ?? p.purchase_price ?? 0),
      price: Number(p.price ?? p.sale_price ?? 0),
      quantity: Number(p.quantity ?? p.qty ?? 0),
      minQty: Number(p.minQty ?? p.min_qty ?? 0),
      expiryDate: p.expiryDate || p.expiry_date || null,
    }));

    setProducts(normalized);
  } catch (err) {
    console.error("loadProducts error:", err);
    toast.error("‚ö†Ô∏è ÿÆÿ∑ÿ£ ŸÅŸä ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™");
  }
};


  useEffect(() => {
    loadProducts();
  }, []);

  if (!hasPermission(["admin", "pharmacist"])) {
    return (
      <div dir="rtl" className="flex items-center justify-center min-h-screen p-6 bg-slate-50">
        <div className="px-6 py-4 text-sm font-medium text-red-700 border border-red-200 bg-red-50 rounded-xl">
          ‚ö†Ô∏è ŸÑÿß ŸäŸÖŸÉŸÜŸÉ ÿØÿÆŸàŸÑ Ÿáÿ∞Ÿá ÿßŸÑÿµŸÅÿ≠ÿ©. ÿßŸÑÿ±ÿ¨ÿßÿ° ÿßŸÑÿ™ŸàÿßÿµŸÑ ŸÖÿπ ŸÖÿØŸäÿ± ÿßŸÑŸÜÿ∏ÿßŸÖ ŸÑÿ™ÿ≠ÿØŸäÿ´ ÿµŸÑÿßÿ≠Ÿäÿßÿ™ŸÉ.
        </div>
      </div>
    );
  }

  // ÿßŸÑŸÅÿ¶ÿßÿ™ ŸàÿßŸÑÿ¥ÿ±ŸÉÿßÿ™ ÿßŸÑŸÖÿ™ÿßÿ≠ÿ© ŸÑŸÑŸÅŸÑÿßÿ™ÿ±
  const categories = [
    "all",
    ...new Set(products.map((p) => p.category).filter(Boolean)),
  ];

  const companies = [
    "all",
    ...new Set(products.map((p) => p.company).filter(Boolean)),
  ];

 

  // ÿ•ÿ≠ÿµÿßÿ¶Ÿäÿßÿ™ ÿ≥ÿ±Ÿäÿπÿ© ŸÑŸÑŸÄ Dashboard
  const stats = useMemo(() => {
    const totalProducts = products.length;
    const totalQty = products.reduce(
      (sum, p) => sum + (Number(p.quantity) || 0),
      0
    );
    const stockValue = products.reduce(
      (sum, p) =>
        sum +
        (Number(p.price) || 0) * (Number(p.quantity) || 0),
      0
    );

    return {
      totalProducts,
      totalQty,
      stockValue,
    };
  }, [products]);

  // ŸÅŸÑÿ™ÿ±ÿ© ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™
  const filteredProducts = useMemo(() => {
    let result = [...products];

    if (search.trim() !== "") {
      result = result.filter((p) =>
        (p.name || "").toLowerCase().includes(search.toLowerCase())
      );
    }

    if (categoryFilter !== "all") {
      result = result.filter((p) => p.category === categoryFilter);
    }

    if (companyFilter !== "all") {
      result = result.filter((p) => p.company === companyFilter);
    }

    if (filterLowStock) {
      result = result.filter((p) => (p.quantity || 0) <= (p.minQty || 0));
    }

    if (filterNearExpiry) {
      result = result.filter((p) => {
        if (!p.expiryDate) return false;
        const days =
          (new Date(p.expiryDate) - new Date()) /
          (1000 * 60 * 60 * 24);
        return days > 0 && days <= 30;
      });
    }

    if (filterExpired) {
      result = result.filter((p) => {
        if (!p.expiryDate) return false;
        const days =
          (new Date(p.expiryDate) - new Date()) /
          (1000 * 60 * 60 * 24);
        return days < 0;
      });
    }

    result.sort((a, b) => {
      const aName = a.name || "";
      const bName = b.name || "";
      if (sortByName === "asc") return aName.localeCompare(bName);
      return bName.localeCompare(aName);
    });

    return result;
  }, [
    search,
    categoryFilter,
    companyFilter,
    sortByName,
    filterLowStock,
    filterNearExpiry,
    filterExpired,
    products,
  ]);

  const openDetails = (p) => {
    setSelectedProduct(p);
    setShowDetails(true);
  };

  // ÿ≠ÿ∞ŸÅ ŸÖŸÜÿ™ÿ¨ ŸÖŸÜ ÿßŸÑÿ®ÿßŸÉ-ÿ•ŸÜÿØ
  const deleteProduct = async (id) => {
    const ok = confirm("ŸáŸÑ ÿ£ŸÜÿ™ ŸÖÿ™ÿ£ŸÉÿØ ŸÖŸÜ ÿ≠ÿ∞ŸÅ ÿßŸÑŸÖŸÜÿ™ÿ¨ÿü");
    if (!ok) return;

    try {
      await api.delete(`/products/${id}`);
      setProducts((prev) => prev.filter((p) => p.id !== id));
      toast.success("ÿ™ŸÖ ÿ≠ÿ∞ŸÅ ÿßŸÑŸÖŸÜÿ™ÿ¨");
    } catch (err) {
      console.error("deleteProduct error:", err);
      toast.error("ÿÆÿ∑ÿ£ ŸÅŸä ÿ≠ÿ∞ŸÅ ÿßŸÑŸÖŸÜÿ™ÿ¨");
    }
  };

  // ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨ ÿ¨ÿØŸäÿØ ŸÑŸÑÿ®ÿßŸÉ-ÿ•ŸÜÿØ
  const handleAddProduct = async () => {
    if (!newProduct.name || !newProduct.price) {
      toast.error("ÿßŸÑÿßÿ≥ŸÖ ŸàÿßŸÑÿ≥ÿπÿ± ŸÖÿ∑ŸÑŸàÿ®ÿßŸÜ ÿπŸÑŸâ ÿßŸÑÿ£ŸÇŸÑ");
      return;
    }

    try {
      const payload = {
        name: newProduct.name,
        sku: newProduct.sku,
        category: newProduct.category,
        company: newProduct.company,
        purchasePrice: Number(newProduct.purchasePrice) || 0,
        price: Number(newProduct.price) || 0,
        quantity: Number(newProduct.quantity) || 0,
        minQty: Number(newProduct.minQty) || 0,
        expiryDate: newProduct.expiryDate || null,
      };

      const res = await api.post("/products", payload);

      setProducts((prev) => [...prev, res.data]);

      setShowAddModal(false);
      setNewProduct({
        name: "",
        sku: "",
        category: "",
        company: "",
        purchasePrice: "",
        price: "",
        quantity: "",
        minQty: 5,
        expiryDate: "",
      });
      toast.success("ÿ™ŸÖ ÿ•ÿ∂ÿßŸÅÿ© ÿßŸÑŸÖŸÜÿ™ÿ¨ ÿ®ŸÜÿ¨ÿßÿ≠");
    } catch (err) {
      console.error("handleAddProduct error:", err);
      const msg = err.response?.data?.message || "ŸÅÿ¥ŸÑ ÿ•ÿ∂ÿßŸÅÿ© ÿßŸÑŸÖŸÜÿ™ÿ¨";
      toast.error(msg);
    }
  };

  const printProducts = () => {
    const w = window.open("", "", "width=900,height=700");

    w.document.write(`
      <html dir="rtl" lang="ar">
      <head>
        <title>ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™</title>
        <style>
          body { font-family:'Tajawal',sans-serif; padding:20px; }
          h2 { text-align:center; margin-bottom: 10px; }
          p.info { text-align:center; font-size: 12px; color:#64748b; margin:0; }
          table { width:100%; border-collapse:collapse; margin-top:20px; }
          th, td { border:1px solid #ddd; padding:6px; font-size:12px; text-align:right; }
          th { background:#f1f5f9; }
        </style>
      </head>
      <body>
        <h2>üìÑ ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™</h2>
        <p class="info">ÿπÿØÿØ ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™: ${products.length} | ÿ™ŸÖ ÿßŸÑÿ™ŸàŸÑŸäÿØ ŸÅŸä: ${new Date().toLocaleString(
          "ar-EG"
        )}</p>
        <table>
          <thead>
            <tr>
              <th>ÿßŸÑÿßÿ≥ŸÖ</th>
              <th>ÿßŸÑŸÉŸàÿØ</th>
              <th>ÿßŸÑŸÅÿ¶ÿ©</th>
              <th>ÿßŸÑÿ¥ÿ±ŸÉÿ©</th>
              <th>ÿ≥ÿπÿ± ÿßŸÑÿ¥ÿ±ÿßÿ°</th>
              <th>ÿ≥ÿπÿ± ÿßŸÑÿ®Ÿäÿπ</th>
              <th>ÿßŸÑŸÉŸÖŸäÿ©</th>
              <th>ÿ±ÿ®ÿ≠/Ÿàÿ≠ÿØÿ©</th>
              <th>ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑÿ±ÿ®ÿ≠</th>
              <th>ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ©</th>
            </tr>
          </thead>
          <tbody>
            ${products
              .map((p) => {
                const unitProfit =
                  (p.price || 0) - (p.purchasePrice || 0);
                const totalProfit = unitProfit * (p.quantity || 0);
                return `
                  <tr>
                    <td>${p.name || ""}</td>
                    <td>${p.sku || ""}</td>
                    <td>${p.category || ""}</td>
                    <td>${p.company || ""}</td>
                    <td>${p.purchasePrice || 0}</td>
                    <td>${p.price || 0}</td>
                    <td>${p.quantity || 0}</td>
                    <td>${unitProfit.toFixed(2)}</td>
                    <td>${totalProfit.toFixed(2)}</td>
                    <td>${p.expiryDate || ""}</td>
                  </tr>
                `;
              })
              .join("")}
          </tbody>
        </table>
        <script>window.print()</script>
      </body>
      </html>
    `);

    w.document.close();
  };

  return (
    <Layout user={user} title="ÿ•ÿØÿßÿ±ÿ© ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™">
      <div dir="rtl" className="space-y-6">

        {/* ÿ±ÿ£ÿ≥ ÿßŸÑÿµŸÅÿ≠ÿ© + ÿßŸÑÿ•ÿ≠ÿµÿßÿ¶Ÿäÿßÿ™ + ÿßŸÑÿ£ÿ≤ÿ±ÿßÿ± */}
        <div className="flex flex-col gap-4 md:flex-row md:items-center md:justify-between">
          <div className="space-y-1">
            <h1 className="flex items-center gap-2 text-2xl font-bold text-slate-800">
              üíä ÿ•ÿØÿßÿ±ÿ© ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™
            </h1>
            <p className="text-sm text-slate-500">
              ŸÖÿ™ÿßÿ®ÿπÿ© ŸÖÿÆÿ≤ŸàŸÜ ÿßŸÑÿ£ÿØŸàŸäÿ©ÿå ŸáŸàÿßŸÖÿ¥ ÿßŸÑÿ±ÿ®ÿ≠ÿå Ÿàÿ™ÿ≠ÿ∞Ÿäÿ±ÿßÿ™ ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ© ŸÖŸÜ Ÿàÿßÿ¨Ÿáÿ© Ÿàÿßÿ≠ÿØÿ©.
            </p>
          </div>

          <div className="flex flex-wrap gap-2">
            <button
              onClick={() => setShowAddModal(true)}
              className="inline-flex items-center gap-2 px-4 py-2 text-sm font-medium text-white rounded-lg shadow-sm bg-emerald-600 hover:bg-emerald-700"
            >
              <span>‚ûï</span>
              <span>ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨</span>
            </button>

            <button
              onClick={printProducts}
              className="inline-flex items-center gap-2 px-4 py-2 text-sm font-medium text-white bg-indigo-600 rounded-lg shadow-sm hover:bg-indigo-700"
            >
              <span>üñ®Ô∏è</span>
              <span>ÿ∑ÿ®ÿßÿπÿ© ÿ™ŸÇÿ±Ÿäÿ±</span>
            </button>

            {printInventoryReport && (
              <button
                onClick={printInventoryReport}
                className="inline-flex items-center gap-2 px-4 py-2 text-sm font-medium text-indigo-700 border border-indigo-100 rounded-lg bg-indigo-50 hover:bg-indigo-100"
              >
                <span>üì•</span>
                <span>ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑÿ¨ÿ±ÿØ (PDF)</span>
              </button>
            )}
          </div>
        </div>

        {/* ŸÉÿ±Ÿàÿ™ ÿßŸÑÿ•ÿ≠ÿµÿßÿ¶Ÿäÿßÿ™ */}
        <div className="grid grid-cols-1 gap-4 md:grid-cols-3">
          <StatCard
            label="ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™"
            value={stats.totalProducts.toLocaleString("ar-EG")}
            icon="üì¶"
            color="bg-sky-50 text-sky-700 border-sky-100"
          />
          <StatCard
            label="ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÉŸÖŸäÿ© ÿ®ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ"
            value={stats.totalQty.toLocaleString("ar-EG")}
            icon="üìä"
            color="bg-emerald-50 text-emerald-700 border-emerald-100"
          />
          <StatCard
            label="ŸÇŸäŸÖÿ© ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ (ÿ™ŸÇÿ±Ÿäÿ®Ÿäÿ©)"
            value={`${stats.stockValue.toFixed(2).toLocaleString("en-US")} ÿ±.ÿ≥`}
            icon="üí∞"
            color="bg-amber-50 text-amber-700 border-amber-100"
          />
        </div>

        {/* ÿßŸÑŸÅŸÑÿßÿ™ÿ± ŸàÿßŸÑÿ®ÿ≠ÿ´ */}
        <div className="p-4 space-y-4 bg-white border shadow-sm rounded-2xl">
          <div className="relative">
            <span className="absolute text-slate-400 left-3 top-2.5">üîé</span>
            <input
              type="text"
              placeholder="ÿßÿ®ÿ≠ÿ´ ÿπŸÜ ŸÖŸÜÿ™ÿ¨ ÿ®ÿßŸÑÿßÿ≥ŸÖÿå ÿßŸÑŸÉŸàÿØÿå ÿ£Ÿà ÿßŸÑÿ¥ÿ±ŸÉÿ©‚Ä¶"
              className="w-full p-3 pr-3 text-sm border rounded-xl pl-9 border-slate-200 focus:outline-none focus:ring-2 focus:ring-emerald-500/60 focus:border-emerald-500"
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />
          </div>

          <div className="flex flex-wrap items-center gap-3 text-sm">
            <div className="flex flex-wrap items-center gap-2">
              <span className="text-xs text-slate-500">ÿßŸÑŸÅÿ¶ÿ©:</span>
              <select
                className="p-2 text-xs border rounded-lg border-slate-200 bg-slate-50"
                value={categoryFilter}
                onChange={(e) => setCategoryFilter(e.target.value)}
              >
                {categories.map((cat) => (
                  <option key={cat} value={cat}>
                    {cat === "all" ? "ŸÉŸÑ ÿßŸÑŸÅÿ¶ÿßÿ™" : cat}
                  </option>
                ))}
              </select>
            </div>

            <div className="flex flex-wrap items-center gap-2">
              <span className="text-xs text-slate-500">ÿßŸÑÿ¥ÿ±ŸÉÿ©:</span>
              <select
                className="p-2 text-xs border rounded-lg border-slate-200 bg-slate-50"
                value={companyFilter}
                onChange={(e) => setCompanyFilter(e.target.value)}
              >
                {companies.map((c) => (
                  <option key={c} value={c}>
                    {c === "all" ? "ŸÉŸÑ ÿßŸÑÿ¥ÿ±ŸÉÿßÿ™" : c}
                  </option>
                ))}
              </select>
            </div>

            <div className="flex flex-wrap items-center gap-2">
              <span className="text-xs text-slate-500">ÿ™ÿ±ÿ™Ÿäÿ®:</span>
              <select
                className="p-2 text-xs border rounded-lg border-slate-200 bg-slate-50"
                value={sortByName}
                onChange={(e) => setSortByName(e.target.value)}
              >
                <option value="asc">ÿßÿ≥ŸÖ ÿßŸÑŸÖŸÜÿ™ÿ¨ (ÿ™ÿµÿßÿπÿØŸä)</option>
                <option value="desc">ÿßÿ≥ŸÖ ÿßŸÑŸÖŸÜÿ™ÿ¨ (ÿ™ŸÜÿßÿ≤ŸÑŸä)</option>
              </select>
            </div>

            {/* ŸÅŸÑÿßÿ™ÿ± ÿ≠ÿßŸÑÿ© ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ ŸàÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ© */}
            <div className="flex flex-wrap items-center gap-2 mt-2 md:mt-0">
              <FilterChip
                active={filterLowStock}
                onClick={() => setFilterLowStock(!filterLowStock)}
                label="ŸÉŸÖŸäÿ© ŸÖŸÜÿÆŸÅÿ∂ÿ©"
              />
              <FilterChip
                active={filterNearExpiry}
                onClick={() => setFilterNearExpiry(!filterNearExpiry)}
                label="ŸÇÿ±ÿ® ÿßŸÜÿ™Ÿáÿßÿ° ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ©"
              />
              <FilterChip
                active={filterExpired}
                onClick={() => setFilterExpired(!filterExpired)}
                label="ŸÖŸÜÿ™ŸáŸä ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ©"
              />
            </div>
          </div>
        </div>

        {/* ÿ¨ÿØŸàŸÑ ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™ */}
        <div className="overflow-x-auto bg-white border shadow-sm rounded-2xl">
          <table className="w-full text-sm text-right min-w-[980px]">
            <thead className="text-xs uppercase border-b bg-slate-50 text-slate-500">
              <tr>
                <th className="p-3 font-medium">ÿßŸÑÿßÿ≥ŸÖ</th>
                <th className="p-3 font-medium">ÿßŸÑŸÉŸàÿØ</th>
                <th className="p-3 font-medium">ÿßŸÑŸÅÿ¶ÿ©</th>
                <th className="p-3 font-medium">ÿßŸÑÿ¥ÿ±ŸÉÿ©</th>
                <th className="p-3 font-medium">ÿ≥ÿπÿ± ÿßŸÑÿ¥ÿ±ÿßÿ°</th>
                <th className="p-3 font-medium">ÿ≥ÿπÿ± ÿßŸÑÿ®Ÿäÿπ</th>
                <th className="p-3 font-medium">ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ</th>
                <th className="p-3 font-medium">ÿ±ÿ®ÿ≠/Ÿàÿ≠ÿØÿ©</th>
                <th className="p-3 font-medium">ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑÿ±ÿ®ÿ≠</th>
                <th className="p-3 font-medium">ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ©</th>
                <th className="p-3 font-medium text-center">ÿ™ÿ≠ÿ∞Ÿäÿ±ÿßÿ™</th>
                <th className="p-3 font-medium text-center">ÿ•ÿ¨ÿ±ÿßÿ°ÿßÿ™</th>
              </tr>
            </thead>

            <tbody>
              {filteredProducts.map((p) => {
                const warnings = getWarnings(p);
                const unitProfit =
                  (p.price || 0) - (p.purchasePrice || 0);
                const totalProfit = unitProfit * (p.quantity || 0);

                let expiryText = p.expiryDate || "-";
                if (warnings.includes("‚ùå ÿßŸÑŸÖŸÜÿ™ÿ¨ ŸÖŸÜÿ™ŸáŸä ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ©!")) {
                  expiryText = "ŸÖŸÜÿ™ŸáŸä";
                }

                return (
                  <tr
                    key={p.id}
                    className="transition-colors border-t border-slate-100 even:bg-slate-50/40 hover:bg-slate-100/60"
                  >
                    <td className="p-3 font-medium text-slate-800">
                      {p.name}
                    </td>
                    <td className="p-3 text-slate-600">{p.sku}</td>
                    <td className="p-3 text-slate-600">{p.category}</td>
                    <td className="p-3 text-slate-600">{p.company}</td>
                    <td className="p-3 text-slate-700">
                      {p.purchasePrice || 0} ÿ±.ÿ≥
                    </td>
                    <td className="p-3 text-slate-700">
                      {p.price || 0} ÿ±.ÿ≥
                    </td>
                    <td
                      className={`p-3 ${
                        (p.quantity || 0) <= (p.minQty || 0)
                          ? "text-red-600 font-bold"
                          : "text-slate-800"
                      }`}
                    >
                      {p.quantity}
                    </td>
                    <td className="p-3 text-slate-700">
                      {unitProfit.toFixed(2)} ÿ±.ÿ≥
                    </td>
                    <td className="p-3 text-slate-700">
                      {totalProfit.toFixed(2)} ÿ±.ÿ≥
                    </td>
                    <td className="p-3 text-slate-700">{expiryText}</td>

                    <td className="p-3 text-center">
                      <WarningIndicator warnings={warnings} />
                    </td>

                    <td className="p-3 text-center">
                      <div className="flex flex-wrap justify-center gap-1">
                        <button
                          onClick={() => openDetails(p)}
                          className="px-3 py-1 text-xs font-medium text-indigo-700 rounded-lg bg-indigo-50 hover:bg-indigo-100"
                        >
                          üîç ÿπÿ±ÿ∂
                        </button>

                        <button
                          onClick={() =>
                            router.push(`/inventory?product=${p.id}`)
                          }
                          className="px-3 py-1 text-xs font-medium text-blue-700 rounded-lg bg-blue-50 hover:bg-blue-100"
                        >
                          üì¶ ŸÖÿÆÿ≤ŸàŸÜ
                        </button>

                        <button
                          onClick={() =>
                            router.push(`/products/edit/${p.id}`)
                          }
                          className="px-3 py-1 text-xs font-medium rounded-lg text-amber-700 bg-amber-50 hover:bg-amber-100"
                        >
                          ‚úèÔ∏è ÿ™ÿπÿØŸäŸÑ
                        </button>

                        <button
                          onClick={() => deleteProduct(p.id)}
                          className="px-3 py-1 text-xs font-medium text-red-700 rounded-lg bg-red-50 hover:bg-red-100"
                        >
                          üóëÔ∏è ÿ≠ÿ∞ŸÅ
                        </button>
                      </div>
                    </td>
                  </tr>
                );
              })}

              {!filteredProducts.length && (
                <tr>
                  <td colSpan={12} className="p-6 text-sm text-center text-slate-400">
                    ŸÑÿß ÿ™Ÿàÿ¨ÿØ ŸÜÿ™ÿßÿ¶ÿ¨ ŸÖÿ∑ÿßÿ®ŸÇÿ© ŸÑŸÑÿ®ÿ≠ÿ´ / ÿßŸÑŸÅŸÑÿßÿ™ÿ± ÿßŸÑÿ≠ÿßŸÑŸäÿ©‚Ä¶
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>

        {/* ÿ™ŸÅÿßÿµŸäŸÑ ÿßŸÑŸÖŸÜÿ™ÿ¨ */}
        {showDetails && selectedProduct && (
          <Modal
            title="ÿ™ŸÅÿßÿµŸäŸÑ ÿßŸÑŸÖŸÜÿ™ÿ¨"
            onClose={() => setShowDetails(false)}
            onConfirm={() => setShowDetails(false)}
            confirmLabel="ÿ•ÿ∫ŸÑÿßŸÇ"
          >
            <div className="space-y-2 text-sm" dir="rtl">
              <p><strong>ÿßŸÑÿßÿ≥ŸÖ:</strong> {selectedProduct.name}</p>
              <p><strong>ÿßŸÑŸÉŸàÿØ:</strong> {selectedProduct.sku}</p>
              <p><strong>ÿßŸÑŸÅÿ¶ÿ©:</strong> {selectedProduct.category}</p>
              <p><strong>ÿßŸÑÿ¥ÿ±ŸÉÿ©:</strong> {selectedProduct.company}</p>
              <p><strong>ÿ≥ÿπÿ± ÿßŸÑÿ¥ÿ±ÿßÿ°:</strong> {selectedProduct.purchasePrice || 0} ÿ±.ÿ≥</p>
              <p><strong>ÿ≥ÿπÿ± ÿßŸÑÿ®Ÿäÿπ:</strong> {selectedProduct.price || 0} ÿ±.ÿ≥</p>
              <p><strong>ÿßŸÑŸÉŸÖŸäÿ©:</strong> {selectedProduct.quantity}</p>
              <p><strong>ÿßŸÑÿ≠ÿØ ÿßŸÑÿ£ÿØŸÜŸâ:</strong> {selectedProduct.minQty}</p>
              <p><strong>ÿ™ÿßÿ±ŸäÿÆ ÿßŸÑÿßŸÜÿ™Ÿáÿßÿ°:</strong> {selectedProduct.expiryDate || "-"}</p>

              <div className="mt-3">
                <strong>ÿßŸÑÿ™ÿ≠ÿ∞Ÿäÿ±ÿßÿ™:</strong>
                {getWarnings(selectedProduct).length ? (
                  <ul className="pr-4 mt-1 text-xs text-red-600 list-disc">
                    {getWarnings(selectedProduct).map((w, i) => (
                      <li key={i}>{w}</li>
                    ))}
                  </ul>
                ) : (
                  <p className="mt-1 text-xs text-emerald-600">
                    ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿ™ÿ≠ÿ∞Ÿäÿ±ÿßÿ™ ÿπŸÑŸâ Ÿáÿ∞ÿß ÿßŸÑŸÖŸÜÿ™ÿ¨.
                  </p>
                )}
              </div>
            </div>
          </Modal>
        )}

        {/* ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨ */}
        {showAddModal && (
          <Modal
            title="ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨ ÿ¨ÿØŸäÿØ"
            onClose={() => setShowAddModal(false)}
            onConfirm={handleAddProduct}
            confirmLabel="ÿ•ÿ∂ÿßŸÅÿ©"
          >
            <div className="grid grid-cols-1 gap-3 text-sm md:grid-cols-2" dir="rtl">
              <input
                type="text"
                className="w-full p-2 border rounded-lg border-slate-200"
                placeholder="ÿßÿ≥ŸÖ ÿßŸÑŸÖŸÜÿ™ÿ¨ *"
                value={newProduct.name}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, name: e.target.value })
                }
              />
              <input
                type="text"
                className="w-full p-2 border rounded-lg border-slate-200"
                placeholder="ÿßŸÑŸÉŸàÿØ SKU"
                value={newProduct.sku}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, sku: e.target.value })
                }
              />
              <input
                type="text"
                className="w-full p-2 border rounded-lg border-slate-200"
                placeholder="ÿßŸÑŸÅÿ¶ÿ©"
                value={newProduct.category}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, category: e.target.value })
                }
              />
              <input
                type="text"
                className="w-full p-2 border rounded-lg border-slate-200"
                placeholder="ÿßŸÑÿ¥ÿ±ŸÉÿ©"
                value={newProduct.company}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, company: e.target.value })
                }
              />
              <input
                type="number"
                className="w-full p-2 border rounded-lg border-slate-200"
                placeholder="ÿ≥ÿπÿ± ÿßŸÑÿ¥ÿ±ÿßÿ°"
                value={newProduct.purchasePrice}
                onChange={(e) =>
                  setNewProduct({
                    ...newProduct,
                    purchasePrice: e.target.value,
                  })
                }
              />
              <input
                type="number"
                className="w-full p-2 border rounded-lg border-slate-200"
                placeholder="ÿ≥ÿπÿ± ÿßŸÑÿ®Ÿäÿπ *"
                value={newProduct.price}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, price: e.target.value })
                }
              />
              <input
                type="number"
                className="w-full p-2 border rounded-lg border-slate-200"
                placeholder="ÿßŸÑŸÉŸÖŸäÿ©"
                value={newProduct.quantity}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, quantity: e.target.value })
                }
              />
              <input
                type="number"
                className="w-full p-2 border rounded-lg border-slate-200"
                placeholder="ÿßŸÑÿ≠ÿØ ÿßŸÑÿ£ÿØŸÜŸâ ŸÑŸÑÿ™ŸÜÿ®ŸäŸá"
                value={newProduct.minQty}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, minQty: e.target.value })
                }
              />
              <div className="md:col-span-2">
                <input
                  type="date"
                  className="w-full p-2 border rounded-lg border-slate-200"
                  value={newProduct.expiryDate}
                  onChange={(e) =>
                    setNewProduct({ ...newProduct, expiryDate: e.target.value })
                  }
                />
              </div>
            </div>
            <p className="mt-2 text-xs text-slate-400" dir="rtl">
              ÿßŸÑÿ≠ŸÇŸàŸÑ ÿßŸÑŸÖÿπŸÑŸÖÿ© ÿ®ŸÄ * ŸÖÿ∑ŸÑŸàÿ®ÿ©. ŸäŸÖŸÉŸÜ ÿ™ÿπÿØŸäŸÑ ÿ®ÿßŸÇŸä ÿßŸÑÿ™ŸÅÿßÿµŸäŸÑ ŸÑÿßÿ≠ŸÇŸãÿß ŸÖŸÜ ÿ¥ÿßÿ¥ÿ© ÿ™ÿπÿØŸäŸÑ ÿßŸÑŸÖŸÜÿ™ÿ¨.
            </p>
          </Modal>
        )}
      </div>
    </Layout>
  );
}

// ÿ®ÿ∑ÿßŸÇÿ© ÿ•ÿ≠ÿµÿßÿ¶Ÿäÿ© ÿ®ÿ≥Ÿäÿ∑ÿ©
function StatCard({ label, value, icon, color }) {
  return (
    <div className={`flex items-center justify-between p-4 border rounded-2xl ${color}`}>
      <div className="space-y-1">
        <p className="text-xs font-medium text-slate-500">{label}</p>
        <p className="text-lg font-bold">{value}</p>
      </div>
      <div className="flex items-center justify-center w-10 h-10 text-lg rounded-full bg-white/70">
        {icon}
      </div>
    </div>
  );
}

// ŸÅŸÑÿ™ÿ± ŸÉŸÄ "Chip" ÿßÿ≠ÿ™ÿ±ÿßŸÅŸä
function FilterChip({ active, onClick, label }) {
  return (
    <button
      type="button"
      onClick={onClick}
      className={`px-3 py-1 text-xs rounded-full border transition ${
        active
          ? "bg-emerald-50 text-emerald-700 border-emerald-200"
          : "bg-slate-50 text-slate-600 border-slate-200 hover:bg-slate-100"
      }`}
    >
      {label}
    </button>
  );
}